import loading from '../../components/xuan-loading/xuan-loading.vue'
export default {
	components:{
		loading
	},
	data() {
		return {
		}
	},
	mounted() {
		this.open();
		setTimeout(()=>{
			this.close();
		},2000)
	},
	methods: {
		close:function(){
			this.$refs.loading.close();
		},
		open:function(){
			this.$refs.loading.open();
		},
		callback(){
			console.log("关闭后回掉");
		}
	}
}
